.home {
  margin: 0;
  padding: 0;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}

.hero {
  position: relative;
  max-width: 1400px;
  /* margin-top: 100px; */
  padding-top: 50px;
  margin: auto;
  height: 500px;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  color: white;
}

.hero-content {
  position: absolute;
  z-index: 2;
  text-align: center;
}

.hero-content h1 {
  font-size: 48px;
  margin-bottom: 10px;
}

.hero-content p {
  font-size: 24px;
  margin-bottom: 20px;
}

.hero-button {
  background-color: #ff6347;
  color: white;
  padding: 10px 20px;
  text-decoration: none;
  border-radius: 5px;
  font-size: 18px;
}

.hero-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  filter: brightness(60%);
}

.collections {
  display: flex;
  justify-content: space-around;
  padding: 40px 20px;
  max-width: 1000px;
  margin: auto;
}

.collection-item {
  flex: 1;
  margin: 0 10px;
  text-align: center;
}

.collection-item img {
  width: 100%;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease;
}

.collection-item img:hover {
  transform: scale(1.05);
}

.collection-item h2 {
  margin-top: 10px;
  font-size: 22px;
}

.featured {
  padding: 40px 20px;
  text-align: center;
  max-width: 1000px;
  margin: auto;
}

.featured h2 {
  font-size: 36px;
  margin-bottom: 20px;
}

.featured-grid {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  gap: 20px;
}

.featured-item {
  flex: 1;
  max-width: 30%;
  text-align: center;
}

.featured-item img {
  width: 100%;
  border-radius: 10px;
  transition: transform 0.3s ease;
}

.featured-item img:hover {
  transform: scale(1.05);
}

.featured-item h3 {
  margin-top: 10px;
  font-size: 20px;
}

@media screen and (max-width: 600px) {
  .hero-content h1 {
    font-size: 30px;
  }
  .hero-content p {
    font-size: 15px;
  }
  .hero-content a {
    font-size: 15px;
  }

  .collections {
    flex-direction: column; /* آیتم‌ها را به صورت ستونی نمایش می‌دهد */
    align-items: center;
  }

  .collection-item {
    flex: none;
    width: 100%; /* آیتم‌ها کل عرض را بگیرند */
    margin-bottom: 20px; /* فاصله بین آیتم‌ها */
  }
  .featured h2 {
    font-size: 25px;
    margin-top: -50px;
  }
  .featured {
    margin-top: 40px;
  }
